.modal {
  position: fixed;
  width: 100vw;
  height: 102vh;
  opacity: 0;
  visibility: hidden;
  transition: all 0.3s ease;
  top: 0;
  left: 0;
  display: flex;
  align-items: center;
  justify-content: center;
  transform: opacity $anim;
  z-index: 10;
  box-shadow: $main-text-color 0px 1px 0px;

  &.open {
    visibility: visible;
    opacity: 1;
    transition-delay: 0s;
    transform: opacity $anim;
  }
  &-bg {
    position: absolute;
    background: $bg-color;
    opacity: 0.99;
    width: 100%;
    height: 100%;
  }
  &-container {
    border-radius: 20px;
    background: $bg-color;
    padding: 10px 5px 20px 5px;
    width: 100%;
    max-height: 46vh;
    margin-top: 25px;
    border: 1px solid $main-text-color;
    overflow: scroll;
    z-index: 20;

    @media screen and (min-height: 360px) {
      padding: 10px 20px 20px 20px;
    }

    @media screen and (min-height: 440px) {
      max-height: 60vh;
      padding: 10px 20px 20px 20px;
    }

    @media screen and (min-width: 768px) {
      width: 80%;
      padding: 10px 40px 10px 40px;
      margin-top: 55px;
    }

    @media screen and (min-width: 1200px) {
      width: 60%;
    }
  }

  &-close {
    position: fixed;
    right: 50%;
    bottom: 20px;
    padding: 5px;
    display: flex;
    align-items: center;
    justify-content: center;
    transform: translateX(50%);
    outline: none;
    color: $main-text-color;
    background: $bg-color;
    border: 0px;
    font-weight: bold;
    cursor: pointer;
    border: 1px solid $main-text-color;
    border-radius: 20px;
    font-family: Geneva;
    width: 50px;
    fill: $main-text-color;

    &:hover {
      color: $accent-txt-color;
      fill: $accent-txt-color;
      border: 1px solid $accent-txt-color;
      box-shadow: $accent-txt-color 0px 0.5px 4px;
    }
  }
}

.modal__title {
  position: fixed;
  min-width: 300px;
  top: 85px;
  left: 50%;
  transform: translateX(-50%);
  text-align: center;
  letter-spacing: 2;
  text-transform: uppercase;
  font-size: 16px;
  padding: 5px 10px 5px 10px;
  color: $accent-txt-color;
  z-index: 1000;
  @media screen and (min-width: 768px) {
    top: 100px;
  }

  @media screen and (min-width: 1200px) {
    top: 115px;
  }
}

.modal__item {
  display: flex;
  justify-content: space-between;
  align-items: center;
  gap: 10px;
  margin-top: 10px;
  font-size: 14px;
}

.modal__markup-title {
  display: flex;
  align-items: center;
  gap: 5px;
  font-size: 10px;

  @media screen and (min-width: 340px) {
    font-size: 14px;
  }
}
.modal__markup-name {
  letter-spacing: 1.2;
  font-size: 12px;
  max-width: 120px;
  min-width: 80px;

  @media screen and (min-width: 340px) {
    max-width: 140px;
    min-width: 120px;
    font-size: 14px;
  }
  @media screen and (min-width: 380px) {
    min-width: 150px;
    font-size: 14px;
  }
  @media screen and (min-width: 1200px) {
    min-width: 280px;
    font-size: 14px;
  }
}

.modal__markup-type {
  font-size: 10px;
  color: $accent-txt-color;
  max-width: 220px;
}

.modal__markup-data {
  display: flex;
  align-items: baseline;
  gap: 7px;
}

.modal__markup-time {
  font-size: 10px;
  color: $accent-txt-color;
}

.modal__markup-price {
  letter-spacing: 1.2;
}

.modal__group-title {
  margin-left: auto;
  margin-right: auto;
}
